import React from "react";
import {BrowserRouter, Route} from "react-router-dom";
import About from "./routes/About";
import Home from "./routes/Home";
import Navigation from "./components/Navigation";
import Detail from "./routes/Detail";

function App() {
  return (
  <BrowserRouter>
    <Navigation />
    <Route path="movie_app_2020/" exact={true} component={Home} />
    <Route path="movie_app_2020/about" component={About} />
    <Route path="movie_app_2020/movie/:id" component={Detail} />
  </BrowserRouter>
  );
}

export default App;